import { useContext } from 'react'
import { useLocation } from 'react-router-dom'
import { UserContext } from '../../context/UserContext'
import Coin from '../../models/Coin'
import {
  BackButton,
  BackLink,
  BackLinkPanel,
  CardRow,
  Container,
  InfoCard,
  RowKey,
  RowValue,
} from './styles'

type Location = {
  state: {
    coin: Coin
  }
}

const CoinInfo = () => {
  const location: Location = useLocation()
  const { coin } = location.state

  const { vsCurrency } = useContext(UserContext)

  const moneyFormatter = Intl.NumberFormat('pt-BR', {
    style: 'currency',
    currency: vsCurrency,
  })

  return (
    <Container id={`coin-${coin.id}`}>
      <InfoCard>
        <CardRow>
          <RowKey id='coin-name'>Nome:</RowKey>
          <RowValue id='coin-name-value'>{coin.name}</RowValue>
        </CardRow>
        <CardRow>
          <RowKey id='coin-symbol'>Símbolo:</RowKey>
          <RowValue id='coin-symbol-value'>{coin.symbol}</RowValue>
        </CardRow>
        <CardRow>
          <RowKey id='coin-price'>Valor:</RowKey>
          <RowValue id='coin-price-value'>
            {moneyFormatter.format(coin.currentPrice)}
          </RowValue>
        </CardRow>
        <CardRow>
          <RowKey id='coin-marketcap'>Capitalização:</RowKey>
          <RowValue id='coin-marketcap-value'>
            {moneyFormatter.format(coin.marketCap)}
          </RowValue>
        </CardRow>
        <CardRow>
          <RowKey id='coin-volume'>Volume total:</RowKey>
          <RowValue id='coin-volume-value'>{coin.totalVolume}</RowValue>
        </CardRow>
        <CardRow>
          <RowKey id='coin-pricechange'>Variação na última hora:</RowKey>
          <RowValue id='coin-pricechange-value'>
            {coin.priceChange1H.toFixed(2)}%
          </RowValue>
        </CardRow>
      </InfoCard>

      <BackLinkPanel>
        <BackLink to='/'>
          <BackButton>Voltar</BackButton>
        </BackLink>
      </BackLinkPanel>
    </Container>
  )
}

export default CoinInfo
